html {
  box-sizing: initial;
}

*, :after, :before {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.page__content {
  position: relative;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

@keyframes circular-motion {
  from {
    transform: rotate(-90deg);
  }

  to {
    transform: rotate(270deg);
  }
}

.stopwatch__minute-hand {
  animation-name: circular-motion;
  animation-duration: get-minutes(60s);
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
}

.stopwatch__second-hand {
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation-name: circular-motion;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch__connecting-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__minute-hand, .stopwatch__second-hand, .stopwatch__connecting-circle {
  position: absolute;
}

.stopwatch__minute-hand, .stopwatch__second-hand {
  transform-origin: 0;
  left: 50%;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}
